const initAccordion = () => {
    const accordions = document.querySelectorAll('.wrock-accordion');
    accordions &&
        accordions.forEach((item) => {
            item.addEventListener('click', (event) => {
                const target = event.target;
                const btn = target.closest('.wrock-accordion__btn');
                if (!btn)
                    return;
                const element = btn.parentElement;
                const content = element.querySelector('.wrock-accordion__content');
                const openItem = item.querySelector('.wrock-accordion__item.open');
                element.classList.toggle('open');
            });
        });
};
export default initAccordion;
